<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible = true"
            ></el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="roleList" border stripe>
        <!--  -->
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row
              :class="['bdBottom', i1 === 0 ? 'bdTop' : '', 'vcenter']"
              v-for="(item1, i1) in scope.row.children"
              :key="item1.id"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag
                  closable
                  @close="removeRightById(scope.row, item1.id)"
                  >{{ item1.authName }}</el-tag
                >
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <!-- for  -->
                <el-row
                  :class="[i2 === 0 ? '' : 'bdTop', 'vcenter']"
                  v-for="(item2, i2) in item1.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      type="success"
                      closable
                      @close="removeRightById(scope.row, item2.id)"
                      >{{ item2.authName }}</el-tag
                    >
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <!--  -->
                  <el-col :span="18">
                    <el-tag
                      v-for="item3 in item2.children"
                      :key="item3.id"
                      type="warning"
                      closable
                      @close="removeRightById(scope.row, item3.id)"
                      >{{ item3.authName }}</el-tag
                    >
                  </el-col>
                </el-row>
              </el-col>
            </el-row>

            <!-- <pre>
              {{ scope.row }}
            </pre> -->
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="#" type="index"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="380px">
          <template slot-scope="scope">
            <!--  -->
            <el-button
              style="padding: 15px"
              size="mini"
              type="primary"
              icon="el-icon-edit"
              circle
              @click="showEditDialog(scope.row.id)"
            ></el-button>
            <!--   -->
            <el-button
              style="padding: 15px"
              size="mini"
              type="danger"
              icon="el-icon-delete"
              circle
              @click="removeRoleById(scope.row.id)"
            ></el-button>
            <!--  -->
            <el-button
              style="padding: 15px"
              size="mini"
              type="warning"
              icon="el-icon-setting"
              circle
              @click="showSetRightDialog(scope.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="50%"
      @close="addDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>

      <!--   -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addRole"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      @close="editDialogClosed"
    >
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editRoleInfo"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="setRightDialogVisible"
      width="50%"
      @close="setRightsDialogClosed"
    >
      <!--  -->
      <el-tree
        :data="rightsList"
        :props="treeProps"
        :default-checked-keys="defKeys"
        show-checkbox
        node-key="id"
        default-expand-all
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allowRights"
          > </el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'shopRoles',
  data() {
    return {
      // 
      roleList: [],

      // 
      // 
      addDialogVisible: false,
      // 
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      // 
      addFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '3~10',
            trigger: 'blur'
          }
        ],
        roleDesc: [{ required: false }]
      },
      // 
      // 
      editDialogVisible: false,
      // 
      editForm: {},
      // 
      editFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 1 }
        ],
        roleDesc: [{ required: false }]
      },

      // 
      setRightDialogVisible: false,
      // 
      rightsList: [],
      // 
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      // ID
      defKeys: [],
      // ID
      roleId: ''
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    // 
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      // console.log(res)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.roleList = res.data
      console.log(this.roleList)
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addRole() {
      // 
      this.$refs.addFormRef.validate(async (valid) => {
        // console.log(valid) true false
        if (!valid) return

        // 
        const { data: res } = await this.$http.post('roles', this.addForm)
        // console.log(res)
        if (res.meta.status !== 201) {
          return this.$message.error('!')
        }
        this.$message.success('!')
        // 
        this.addDialogVisible = false
        // 
        this.getRolesList()
      })
    },
    // 
    async showEditDialog(id) {
      // console.log(id)
      // id
      const { data: res } = await this.$http.get('roles/' + id)
      // console.log(res)

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      //   data 
      this.editForm = res.data

      this.editDialogVisible = true
    },
    // 
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    // 
    editRoleInfo() {
      this.$refs.editFormRef.validate(async (valid) => {
        // console.log(valid)
        if (!valid) return
        // 
        console.log(this.editForm)
        const { data: res } = await this.$http.put(
          'roles/' + this.editForm.roleId,
          {
            roleName: this.editForm.roleName,
            roleDesc: this.editForm.roleDesc
          }
        )
        console.log(res)
        // 
        if (res.meta.status !== 200) {
          this.editDialogVisible = false
          return this.$message.error('')
        }

        // 
        this.editDialogVisible = false
        // 
        this.getRolesList()
        // 
        this.$message.success('!')
      })
    },
    // 
    // ID
    async removeRoleById(id) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      // confirm
      // cancel
      // console.log(confirmResult, typeof confirmResult)

      if (confirmResult !== 'confirm') {
        return this.message.info('')
      }
      // console.log('')

      // 
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      this.$message.success('!')
      this.getRolesList()
    },
    // id 
    async removeRightById(role, rightId) {
      // 
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)

      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }

      // console.log('')
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // this.getRolesList()
      role.children = res.data
    },

    // 
    async showSetRightDialog(role) {
      // data  
      this.roleId = role.id
      console.log('id', role.id)
      // 
      const { data: res } = await this.$http.get('rights/tree')

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // data
      this.rightsList = res.data
      console.log(this.rightsList)

      // ID
      this.getLeafKeys(role, this.defKeys)

      this.setRightDialogVisible = true
    },

    // ID  defKeys 
    getLeafKeys(node, arr) {
      // node children  
      if (!node.children) {
        return arr.push(node.id)
      }

      // return 
      node.children.forEach(item => this.getLeafKeys(item, arr)
      )
    },

    // 
    setRightsDialogClosed() {
      this.defKeys = []
    },

    // 
    async allowRights() {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      // console.log(keys)
      const isStr = keys.join(',')

      const { data: res } = await this.$http.post(`roles/${this.roleId}/rights`, { rids: isStr })

      if (res.meta.status !== 200) {
        return this.$message.error('!')
      }
      console.log(res)

      this.$message.success('')

      this.getRolesList()

      this.setRightDialogVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 7px;
}

.bdTop {
  border-top: 1px solid #eee;
}

.bdBottom {
  border-bottom: 1px solid #eee;
}

.vcenter {
  display: flex;
  align-items: center;
}
.el-row {
  padding: 10px 30px;
}
</style>
