<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert
        title=""
        type="info"
        center
        show-icon
        :closable="false"
      >
      </el-alert>

      <!--  -->
      <el-steps
        :space="200"
        :active="activeIndex - 0"
        finish-status="success"
        align-center
      >
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addFormRef"
        label-width="100px"
        label-position="top"
      >
        <!-- tab -->
        <el-tabs
          v-model="activeIndex"
          :tab-position="'left'"
          :before-leave="beforeTabLeave"
          @tab-click="tabClicked"
        >
          <!--  -->
          <el-tab-pane label="" name="0">
            <el-form-item label="" prop="goods_name">
              <el-input v-model="addForm.goods_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_price">
              <el-input v-model="addForm.goods_price" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_weight">
              <el-input v-model="addForm.goods_weight" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_number">
              <el-input v-model="addForm.goods_number" type="number"></el-input>
            </el-form-item>
            <el-form-item label="" prop="goods_cat">
              <el-cascader
                expand-trigger="hover"
                :options="cateList"
                :props="cateProps"
                v-model="addForm.goods_cat"
                @change="handleChange"
              >
              </el-cascader>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="1">
            <!-- item -->
            <el-form-item
              :label="item.attr_name"
              v-for="item in manyTableData"
              :key="item.attr_id"
            >
            <!--  -->
              <el-checkbox-group v-model="item.attr_vals">
               <el-checkbox :label="cb" v-for="(cb, i) in item.attr_vals" :key="i" border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>

          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="2">
            <el-form-item :label="item.attr_name" v-for="item in onlyTableData" :key="item.attr_id">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="3">
             <!-- action API-->
             <!-- https://lianghj.top:8888/api/private/v1/upload -->
             <!-- on-preview :  -->
            <!-- list-type -->
            <el-upload
              :action="uploadURL"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              list-type="picture"
              :headers="headerObj"
              :on-success="handleSuccess"
              >
              <el-button size="small" type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label="" name="4">
            <!--  -->
             <quill-editor v-model="addForm.goods_introduce"></quill-editor>
             <!--  -->
             <el-button type="primary" class="btnAdd" @click="add"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
      <!--  -->
    </el-card>

    <!--   -->
    <el-dialog
      title=""
      :visible.sync="previewDialogVisible"
      width="50%"
    >
      <img :src="previewPath" alt="" class="previewImg">
    </el-dialog>
  </div>
</template>

<script>
//  lodash
import _ from 'lodash'

export default {
  data() {
    return {
      activeIndex: '0',
      // *
      addForm: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        // 
        goods_cat: [],
        // 
        pics: [],
        //  
        goods_introduce: '',
        attrs: []
      },
      // 
      addFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cat: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      cateList: [],
      cateProps: {
        label: 'cat_name',
        value: 'cat_id',
        children: 'children'
      },
      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // url
      //   https://lianghj.top:8888/api/private/v1/upload
      uploadURL: 'http://127.0.0.1:8888/api/private/v1/upload',
      // uploadURL: 'https://lianghj.top:8888/api/private/v1/upload',
      // headers
      headerObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      previewPath: '',
      previewDialogVisible: false
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    async getCateList() {
      // 
      const { data: res } = await this.$http.get('categories')

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      this.cateList = res.data

      console.log(this.cateList)
    },
    // *
    handleChange() {
      console.log(this.addForm.goods_cat)

      if (this.addForm.goods_cat.length !== 3) {
        this.addForm.goods_cat = []
      }
    },
    // *
    beforeTabLeave(activeName, oldActiveName) {
      // console.log(activeName, oldActiveName)

      if (oldActiveName === '0' && this.addForm.goods_cat.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    // * 
    async tabClicked() {
      console.log(this.activeIndex)
      // 
      if (this.activeIndex === '1') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          { params: { sel: 'many' } }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('')
        }

        console.log(res)

        res.data.forEach((item) => {
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })

        // this.$message.success('')
        this.manyTableData = res.data
      } else if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(
          `categories/${this.cateId}/attributes`,
          { params: { sel: 'only' } }
        )

        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        console.log(res.data)

        this.onlyTableData = res.data
      }
    },
    // *
    handlePreview(file) {
      console.log(file)
      this.previewPath = file.response.data.url
      this.previewDialogVisible = true
    },
    // *
    handleRemove(file) {
      // 1.
      const filePath = file.response.data.tmp_path
      // 2. pics 
      const i = this.addForm.pics.findIndex(x => x.pic === filePath)
      // 3. splice    pics 
      this.addForm.pics.splice(i, 1)
      console.log(this.addForm)
    },
    // *
    handleSuccess(response) {
      console.log(response)
      // 1.
      const picInfo = { pic: response.data.tmp_path }
      // 2.push  PICS 
      this.addForm.pics.push(picInfo)
      console.log(this.addForm)
    },
    // * 
    add() {
      console.log(this.addForm)
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) {
          return this.$message.error('')
        }
        // 
        // lodash  cloneDeep(obj)
        const form = _.cloneDeep(this.addForm)
        form.goods_cat = form.goods_cat.join(',')

        // 
        this.manyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals.join('') }
          this.addForm.attrs.push(newInfo)
        })
        // 
        this.onlyTableData.forEach(item => {
          const newInfo = { attr_id: item.attr_id, attr_value: item.attr_vals }
          this.addForm.attrs.push(newInfo)
        })
        // 
        form.attrs = this.addForm.attrs
        console.log(form)

        // ,
        // *===>>>
        const { data: res } = await this.$http.post('goods', form)

        console.log('$$$$', res)

        // 
        if (res.meta.status !== 201) {
          return this.$message.error('!')
        }

        this.$message.success('')
        //  
        this.$router.push('/goods')
      })
    }
  },
  // 
  computed: {
    cateId() {
      if (this.addForm.goods_cat.length === 3) {
        return this.addForm.goods_cat[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
  margin-right: 5px !important;
}
.previewImg{
  width: 100%;
  height: 100%;
}
.btnAdd{
  margin: 15px 0;
}

</style>
