<template>
  <div class="login-container">
    <div class="login_box">
      <!--  -->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt="" />
      </div>
      <!--  -->
      <el-form
        ref="loginFormRef"
        :model="loginForm"
        :rules="loginFormRules"
        label-width="80px"
      >
          <!--  -->
          <el-form-item label="" class="info" prop="username">
            <el-input
              v-model="loginForm.username"
              suffix-icon="iconfont icon-user"
            ></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item label="" class="info" prop="password">
            <el-input
              v-model="loginForm.password"
              suffix-icon="iconfont icon-3702mima"
              type="password"
            ></el-input>
          </el-form-item>
          <!--  -->
          <el-form-item class="btns">
            <el-button type="primary" @click="login"></el-button>
            <el-button type="info" @click="resetLoginForm"></el-button>
          </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'shopLogin',
  data () {
    return {
      // 
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      // 
      loginFormRules: {
        // 
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: ' 3  10 ',
            trigger: 'blur'
          }
        ],
        // 
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 6,
            max: 15,
            message: ' 6  15 ',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    // 
    resetLoginForm () {
      // console.log(this) VC
      this.$refs.loginFormRef.resetFields()
    },
    // 
    login () {
      this.$refs.loginFormRef.validate(async (valid) => {
        console.log(valid)
        //  true FALSE
        if (!valid) return
        const { data: res } = await this.$http.post('login', this.loginForm)
        console.log(res)
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        // *1 token sessionStorage 
        // *   1.1 API
        // *   1.2 token  token  sessionStorage 

        window.sessionStorage.setItem('token', res.data.token)
        // *2 /home
        // this.$router.push('/users')
        this.$router.push('/welcome')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login-container {
  background-color: #2b4a6b;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.login_box {
  width: 90%;
  max-width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  position: relative;

  .avatar_box {
    position: absolute;
    top: -75px;
    box-sizing: border-box;
    background-color: transparent;
    height: 130px;
    padding-top: 20px;
    width: 130px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 1px 3px 6px #0000001a, -2px -3px 6px #0000001a;
    img {
      width: 80%;
      height: auto;
    }
  }
  .el-form {
    box-sizing: border-box;
    position: absolute;
    bottom: 0;
    width: 100%;
    padding: 0 20px;
  }
}

.btns {
  float: right;
}
.info {
  color: #555;
  font-size: 18px;
}
</style>
