<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddCateDialog"
            ></el-button
          >
        </el-col>
      </el-row>

      <!--  -->
      <tree-table
        :data="cateList"
        :columns="columns"
        :selection-type="false"
        :expand-type="false"
        border
        show-index
        class="treeTable"
        index-text="#"
      >
        <!--  -->
        <template slot="isOk" slot-scope="scope">
          <i
            v-if="scope.row.cat_deleted === false"
            style="color: lightgreen"
            class="el-icon-success"
          ></i>
          <i v-else class="el-icon-error" style="color: lightgreen"></i>
        </template>

        <!--  -->
        <template slot="order" slot-scope="scope">
          <el-tag size="mini" v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag
            size="mini"
            v-else-if="scope.row.cat_level === 1"
            type="success"
            ></el-tag
          >
          <el-tag size="mini" v-else type="warning"></el-tag>
        </template>

        <!--  -->
        <template slot="opt" slot-scope="">
          <!--  -->
          <el-button
            type="primary"
            icon="el-icon-edit"
            circle
            size="mini"
          ></el-button>
          <!--  -->
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            size="mini"
          ></el-button>
        </template>
      </tree-table>

      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 3, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="addCateDialogVisible"
      width="50%"
      @close="addCateDialogClosed"
    >
      <!--  -->
      <el-form
        :model="addCateForm"
        :rules="addCateFormRules"
        ref="addCateFormRef"
        label-width="100px"
      >
        <el-form-item label=":" prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label=":">
          <!-- options  -->
          <!-- v-model -->
          <el-cascader
            v-model="selectKeys"
            :options="parentCateList"
            :props="cascaderProps"
            @change="parentCateChanged"
            expand-trigger="hover"
            clearable
            change-on-select
          ></el-cascader>
        </el-form-item>
      </el-form>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addCate"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 
      queryInfo: {
        type: 3,
        pagenum: 1,
        pagesize: 5
      },
      // 
      cateList: [],
      // 
      total: 0,
      // table
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'isOk'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'order'
        },
        {
          label: '',
          // 
          type: 'template',
          // 
          template: 'opt'
        }
      ],
      // 
      addCateDialogVisible: false,
      // 
      addCateForm: {
        // 
        cat_name: '',
        // ID
        cat_pid: 0,
        // 
        cat_level: 0
      },
      // 
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      parentCateList: [],
      // 
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // id 
      selectKeys: []
    }
  },
  created() {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })

      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      console.log('data', res.data)
      // this.cateList
      console.log(res.data)
      //  cateList
      this.cateList = res.data.result
      // 
      this.total = res.data.total
    },
    //  pagesize 
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getCateList()
    },
    //  pageNum 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getCateList()
    },
    //  
    showAddCateDialog() {
      //   
      this.getParentCateList()
      // 
      this.addCateDialogVisible = true
    },
    // 
    async getParentCateList() {
      const { data: res } = await this.$http.get('categories', {
        params: { type: 2 }
      })

      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      console.log('@@@', res.data)

      this.parentCateList = res.data
      console.log('$$$', this.parentCateList)
    },
    //  
    parentCateChanged() {
      console.log(this.selectKeys)
      //  selectKeys  length 0 
      // 
      if (this.selectKeys.length > 0) {
        // id
        this.addCateForm.cat_pid = this.selectKeys[this.selectKeys.length - 1]
        // 
        this.addCateForm.cat_level = this.selectKeys.length
        return null
      } else {
        // id
        this.addCateForm.cat_pid = 0
        // 
        this.addCateForm.cat_level = 0
      }
    },
    // 
    addCate() {
      console.log(this.addCateForm)
      this.$refs.addCateFormRef.validate(async (valid) => {
        if (!valid) return
        const { data: res } = await this.$http.post(
          'categories',
          this.addCateForm
        )

        if (res.meta.status !== 201) {
          return this.$message.error('')
        }

        this.$message.success('')
        this.getCateList()
        this.addCateDialogVisible = false
      })
    },
    //  
    addCateDialogClosed() {
      this.$refs.addCateFormRef.resetFields()
      this.selectKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    }
  }
}
</script>

<style lang="less" scoped>
.el-button {
  padding: 15px !important;
}
.el-icon-error,
.el-icon-success {
  font-size: 30px;
}
.treeTable {
  margin-top: 15px;
}
.el-cascader {
  width: 100%;
}
</style>
